export default {
	data() {
		return {
			wenanData:[
				{
					text:'舔狗日记',
					id:1
				},{
					text:'网易云热评',
					id:2
				},{
					text:'土味情话',
					id:3
				},{
					text:'彩虹屁',
					id:4
				},{
					text:'经典台词',
					id:5
				}
			],
			toolData:[
				{
					text:'垃圾分类',
					id:1
				},{
					text:'宠物识别',
					id:2
				}
			],
			showWenanType:false,
			showToolType:false
		}
	},
	onLoad() {

	},
	methods: {
		showtype:function(e) {
			var type = e.currentTarget.dataset.type
			if(type == 'gj'){
				this.showToolType = !this.showToolType
			}else{
				this.showWenanType = !this.showWenanType
			}
			
		},
		goSecondPage:function(e){
			var id = e.currentTarget.dataset.id;
			var url;
			if(id == 1){
				url = '../tgrj/tgrj'
			}else if(id == 2 || id == 3 || id == 4){
				url = '../wyyrp/wyyrp?id=' + id
			}else if(id == 5){
				url = '../jdtc/jdtc'
			}
			uni.navigateTo({
				url:url
			})
		},
		goToolSecondPage:function(e){
			var id = e.currentTarget.dataset.id;
			var url;
			if(id == 1){
				url = '../rubbishsort/rubbishsort'
			}else if(id == 2){
				url = '../wyyrp/wyyrp?id=' + id
			}
			uni.navigateTo({
				url:url
			})
		}
	}
}
